import { IPureAction } from "store/types";
import { ActionTypes } from "data/actionTypes";
import { IActivity, IActivityState } from "data/activity";
import API from "store/api";
import { store } from "_helpers";

// Fetch request to API
export const fetchActivitiesRequest = async () => {
  try {
    const response = await API.get(`posts`);
    return store.dispatch(fetchSuccess(response.data));
  } catch (error) {
    console.log(error);
    return store.dispatch(fetchError(error));
  }
};

// Set activities
export const fetchSuccess = (
  data: IActivityState
): IPureAction<ActionTypes.GET_ACTIVITIES_SUCCESS, IActivityState> => ({
  type: ActionTypes.GET_ACTIVITIES_SUCCESS,
  payload: data,
});

// Error if fetch has failed
export const fetchError = (
  error: Error
): IPureAction<ActionTypes.GET_ACTIVITIES_FAILURE, Error> => ({
  type: ActionTypes.GET_ACTIVITIES_FAILURE,
  payload: error,
});

// Add new activity
export const addActivity = (
  data: IActivity
): IPureAction<ActionTypes.ADD_ACTIVITY, IActivity> => ({
  type: ActionTypes.ADD_ACTIVITY,
  payload: data,
});

// Edit activity
export const editActivity = (
  data: IActivity
): IPureAction<ActionTypes.EDIT_ACTIVITY, IActivity> => ({
  type: ActionTypes.EDIT_ACTIVITY,
  payload: data,
});

// Remove Activity
export const removeActivity = (
  id: number
): IPureAction<ActionTypes.REMOVE_ACTIVITY, Number> => ({
  type: ActionTypes.REMOVE_ACTIVITY,
  payload: id,
});

// Remove Activity
export const getActivityById = (
  id: number
): IPureAction<ActionTypes.GET_ACTIVITY_BY_ID, Number> => ({
  type: ActionTypes.GET_ACTIVITY_BY_ID,
  payload: id,
});
