import React from "react";
import { IGoal, IGoalState } from "data/goal";
import { IUser, IUserState } from "data/user";
import { IActivity, IActivityState } from "data/activity";

type DataTypes = IGoalState | IUserState | IActivityState;
type DataType = IGoal | IUser | IActivity;

interface IProps {
  listItems: any[];
  searchTerm: string;
  setSearchResults: any;
  selected: any;
  setSelected: any;
  setNewMode: any;
}

export const List: React.FunctionComponent<IProps> = (props) => {
  const { listItems, selected, setSelected, setNewMode } = props;

  const selectActivity = (activity: IGoal) => {
    setSelected(activity);
    setNewMode(false);
  };

  return (
    <div className="list">
      {/* ACTIVITY */}
      {/* <ReactSortable className="sortable" list={activities} setList={setSearchResults}> */}
      {listItems.map((item) => (
        <div key={item.id}>
          {/* <div>{CategoryTypes[activity.categoryId]}</div> */}
          <div
            className={
              item.id === selected.id ? "list-item selected" : "list-item"
            }
            onClick={() => selectActivity(item)}
          >
            <div
              className="list-item-icon"
              style={{ backgroundColor: item.color }}
            >
              <a>SE</a>
            </div>
            <div className="list-item-data">
              <div className="list-item-title">{item.title ? item.title : item.name}</div>
              <div className="list-item-read-time">5 minutes read</div>
            </div>
          </div>
        </div>
      ))}
      {/* </ReactSortable> */}
    </div>
  );
};

export default List;
