import { Mutator } from "_helpers/createReducer";
import { IActivitiesState } from "store/_reducers/activity.reducer";
import { IActivity } from "data/activity";

type ActivityMutator<P = any> = Mutator<IActivitiesState, P>;

export const getSuccess: ActivityMutator<IActivity> = (state, data) => {
  const newActivity = state.activities.concat(data);
  return {
    ...state,
    activities: newActivity,
  };
};

export const getFailure: ActivityMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error,
  };
};

export const addActivity: ActivityMutator<IActivity> = (state, data) => {
  const newActivity = state.activities.concat(data);
  return {
    ...state,
    activities: newActivity,
  };
};

export const editActivity: ActivityMutator<IActivity> = (state, data) => {
  const updatedItems = state.activities.map((activity) => {
    if (activity.id === data.id) {
      return { ...activity, ...data };
    }
    return activity;
  });
  return { ...state, activities: updatedItems };
};

export const removeActivity: ActivityMutator<number> = (state, data) => {
  console.log("removing item by id:", data);
  return {
    ...state,
    activities: state.activities.filter((activity) => activity.id !== data),
  };
};

export const getActivityById: ActivityMutator<number> = (state, data) => {
  return {
    ...state,
    activities: state.activities.filter((activity) => activity.id !== data),
  };
};
