import { Mutator } from "_helpers/createReducer";
import { IGoalsState } from "store/_reducers/goal.reducer";
import { IGoal } from "data/goal";

type GoalMutator<P = any> = Mutator<IGoalsState, P>;

export const getSuccess: GoalMutator<IGoal> = (state, data) => {
  const newGoal = state.goals.concat(data);
  return {
    ...state,
    goals: newGoal,
  };
};

export const getFailure: GoalMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error,
  };
};

export const addGoal: GoalMutator<IGoal> = (state, data) => {
  const newGoal = state.goals.concat(data);
  return {
    ...state,
    goals: newGoal,
  };
};

export const editGoal: GoalMutator<IGoal> = (state, data) => {
  const updatedItems = state.goals.map((goal) => {
    if (goal.id === data.id) {
      return { ...goal, ...data };
    }
    return goal;
  });
  return { ...state, goals: updatedItems };
};

export const removeGoal: GoalMutator<number> = (state, data) => {
  console.log("removing item by id:", data);
  return {
    ...state,
    goals: state.goals.filter((goal) => goal.id !== data),
  };
};

export const getGoalById: GoalMutator<number> = (state, data) => {
  return {
    ...state,
    goals: state.goals.filter((goal) => goal.id !== data),
  };
};
