import createReducer, { ReducerHandler } from "_helpers/createReducer";
import { IUser } from "data/user";
import { ActionTypes } from "data/actionTypes";
import * as mutators from "../users/mutations";

export interface ILoginState {
  user: IUser;
  loggingIn: boolean;
  error?: Error;
}

const user = JSON.parse(localStorage.getItem("user") || "{}");

const initialState: ILoginState = {
  user: user ? user : {},
  loggingIn: false,
  error: undefined
};

const handlers: ReducerHandler<ILoginState> = {
  [ActionTypes.LOGIN_REQUEST]: mutators.loginRequest,
  [ActionTypes.LOGIN_SUCCESS]: mutators.loginSuccess,
  [ActionTypes.LOGIN_FAILURE]: mutators.loginFailure,
  [ActionTypes.LOGOUT]: mutators.logout,
  [ActionTypes.LOGOUT_SUCCESS]: mutators.logoutSuccess,
  [ActionTypes.LOGOUT_FAILURE]: mutators.logoutFailure
};

export const authenticationReducer = createReducer<ILoginState>(
  initialState,
  handlers
);
