import React, { useEffect, useState, FormEvent } from "react";

import { login, logout } from "store/users/actions";
import { RouteComponentProps } from "react-router-dom";
import { useFormFields } from "hooks/useFormFields";
import Input from "components/UI/Input";

export const Login: React.FunctionComponent<RouteComponentProps> = props => {
  const [fields, handleFieldChange] = useFormFields({
    email: "",
    password: "",
  });
  const [submitted, setSubmitted] = useState(false);

  useEffect(() => {
    logout();
  }, []);

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    setSubmitted(true);
    if (fields) {
      login(fields, props.history);
    }
  };

  return (
    <div className="login-wrapper">
      <div className="login-form">
        <h1 className="login-text">Supervise</h1>
        <form name="form" className="form" onSubmit={handleSubmit}>
          <div
            className={
              "form-group" + (submitted && !fields.email ? " has-error" : "")
            }
          >
            <Input id="email" name="email" className="form-control" placeholder="example@student.nhlstenden.com" value={fields.email} onChange={handleFieldChange} />
            {submitted && !fields.email && (
              <div className="help-block">Email is required</div>
            )}
          </div>
          <div
            className={
              "form-group" + (submitted && !fields.password ? " has-error" : "")
            }
          >
            <Input type="password" id="password" name="password" className="form-control" placeholder="************" value={fields.password} onChange={handleFieldChange} />
            {submitted && !fields.password && (
              <div className="help-block">Password is required</div>
            )}
          </div>
          <div className="form-group">
            <button className="btn btn-primary">Login</button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;
