import React, { ReactNode, ComponentType } from "react";
import { Route } from "react-router";
import { RouteComponentProps, RouteProps, Redirect } from "react-router-dom";

import { useUserCheck } from "hooks/useUserInfo";

interface IAppRouteProps extends RouteProps {
  component: ComponentType<any>;
  layout: ComponentType;
}

export const AppRoute = (props: IAppRouteProps) => {
  // Maak een hook die checkt of je ingelogd bent
  // const [userInfo] = useUserCheck();
  // console.log(userInfo);
  const { component: Component, layout: Layout, ...rest } = props;

  const render = (props: RouteComponentProps<any>): ReactNode => {
    return (
      <Layout>
        <Component {...props} />
      </Layout>
    );
  };

  return <Route {...rest} render={render} />;
};
