import React, { useState, FormEvent } from "react";
import { IActivity, IBody } from "data/activity";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { addActivity } from "store/activities/actions";
import { useFormFields } from "hooks/useFormFields";
import Editor from "@stfy/react-editor.js";
import { EDITOR_JS_TOOLS } from "data/tools";

interface IProps {
  setNewMode: any;
  setSelected: any;
}

export const NewSideView: React.FunctionComponent<IProps> = (props) => {
  const { setNewMode, setSelected } = props;

  const dispatch = useTypedDispatch();
  const [fields, handleFieldChange] = useFormFields({
    id: Math.floor(Math.random() * 10000),
    title: "",
    categoryId: 1,
    date: new Date().toJSON().slice(0, 10).replace(/-/g, "/"),
    userId: 13,
  });
  const [editorBody, setEditorBody] = useState<IBody>();

  const tabs = ["Activity", "Goal"];
  const [tabMode, setTabMode] = useState(0);

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(addActivity({ ...fields, body: editorBody }));
    setNewMode(false);
    setSelected(fields);
  };

  return (
    <form onSubmit={handleSubmit}>
      <div className="sideview-functions">
        <div className="item"></div>
      </div>
      <div className="sideview-item">
        <div className="sideview-item-menus">
          {/* Show all tabs and set selected class  */}
          <div className="tabs">
            {tabs.map((tab, index) => (
              <div
                key={index}
                className={index === tabMode ? "tab selected" : "tab"}
              >
                <a onClick={() => setTabMode(index)}>{tab}</a>
              </div>
            ))}
          </div>
          <div className="action">
            <input type="submit" value="save" />
          </div>
        </div>
        <div className="sideview-content">
          <div className="sideview-content-title">
            <input
              id="title"
              placeholder="Add title..."
              value={fields.title}
              onChange={handleFieldChange}
            />
          </div>
          <div id="sideview-content-item" className="sideview-content-item">
            <Editor
              holder="sideview-content-item"
              onData={(data) => setEditorBody(data)}
              tools={EDITOR_JS_TOOLS}
              data={editorBody}
              placeholder="Add text..."
              autofocus
            />
          </div>
        </div>
      </div>
    </form>
  );
};

export default NewSideView;
