import { Mutator } from "_helpers/createReducer";
import { IUserState } from "store/_reducers/user.reducer";
import { ILoginState } from "store/_reducers/authentication.reducer";
import { IUser } from "data/user";

type LoginMutator<P = any> = Mutator<ILoginState, P>;
type UserMutator<P = any> = Mutator<IUserState, P>;

export const loginRequest: LoginMutator<any> = (state, data) => {
  return {
    loggingIn: true,
    user: data
  };
};

export const loginSuccess: LoginMutator<any> = (state, data) => {
  return {
    ...state,
    user: data,
    loggingIn: false
  };
};

export const loginFailure: LoginMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error
  };
};

export const logout: LoginMutator = state => {
  return {
    ...state,
    loggingIn: false
  };
};

export const logoutSuccess: LoginMutator = state => {
  return {
    ...state
  };
};

export const logoutFailure: LoginMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error
  };
};

export const registerRequest: UserMutator<any> = (state, data) => {
  return {
    ...state,
    registering: data
  };
};

export const registersuccess: UserMutator<IUser> = (state, data) => {
  const newUsers = state.users.concat(data);
  return {
    ...state,
    users: newUsers,
    registering: false
  };
};

export const registerFailure: UserMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error,
    registering: false
  };
};

export const getUsersRequest: UserMutator<IUserState> = state => {
  return {
    ...state,
    loading: true
  };
};

export const getUsersSuccess: UserMutator = (state, data) => {
  return {
    ...state,
    users: data,
    loading: false
  };
};

export const getUsersFailure: UserMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error
  };
};

export const editUserRequest: UserMutator<Boolean> = (state, editing) => {
  return { ...state, editing };
};

export const editUserSuccess: UserMutator<IUser> = (state, data) => {
  const updatedItems = state.users.map(user => {
    if (user.id === data.id) {
      return { ...user, ...data };
    }
    return user;
  });
  return { ...state, users: updatedItems, editing: false };
};

export const editUserFailure: UserMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error,
    editing: false
  };
};

export const deleteUserRequest: UserMutator<Boolean> = state => {
  return {
    ...state,
    deleting: true
  };
};

export const deleteUserSuccess: UserMutator<number> = (state, id) => {
  return {
    ...state,
    deleting: false,
    users: state.users.filter(user => user.id !== id)
  };
};

export const deleteUserFailure: UserMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error
  };
};
