import { IPureAction } from "store/users/types";
import { ActionTypes } from "data/actionTypes";
import { userService } from "_services";
// import { IUserState } from "store/_reducers/user.reducer";
import { IUser } from "data/user";
// import API from "store/api";
import { store } from "_helpers";

// Register
export const register = async (user: IUser) => {
  store.dispatch(registerRequest());

  try {
    const response = await userService.register(user);
    return store.dispatch(registerSuccess(response));
  } catch (error) {
    return store.dispatch(registerFailure(error));
  }
};

export const registerRequest = (): IPureAction<
  ActionTypes.REGISTER_REQUEST,
  boolean
> => ({
  type: ActionTypes.REGISTER_REQUEST,
  payload: true,
});

export const registerSuccess = (
  data: IUser
): IPureAction<ActionTypes.REGISTER_SUCCESS, IUser> => ({
  type: ActionTypes.REGISTER_SUCCESS,
  payload: data,
});

export const registerFailure = (
  error: Error
): IPureAction<ActionTypes.REGISTER_FAILURE, Error> => ({
  type: ActionTypes.REGISTER_FAILURE,
  payload: error,
});

// Login
export const login = async (data: any, history: any) => {
  store.dispatch(loginRequest(data.email));

  try {
    const response = await userService.login(data.email, data.password);
    store.dispatch(loginSuccess(response));
    history.push("/");
  } catch (error) {
    return store.dispatch(loginFailure(error));
  }
};

export const loginRequest = (
  data: IUser
): IPureAction<ActionTypes.LOGIN_REQUEST, IUser> => ({
  type: ActionTypes.LOGIN_REQUEST,
  payload: data,
});

export const loginSuccess = (
  data: IUser
): IPureAction<ActionTypes.LOGIN_SUCCESS, IUser> => ({
  type: ActionTypes.LOGIN_SUCCESS,
  payload: data,
});

export const loginFailure = (
  error: Error
): IPureAction<ActionTypes.LOGIN_FAILURE, Error> => ({
  type: ActionTypes.LOGIN_FAILURE,
  payload: error,
});

// Logout
export const logout = async () => {
  try {
    userService.logout();
    store.dispatch(logoutSuccess());
  } catch (error) {
    return store.dispatch(logoutFailure(error));
  }
};

export const logoutSuccess = (): IPureAction<ActionTypes.LOGOUT, void> => ({
  type: ActionTypes.LOGOUT,
  payload: undefined,
});

export const logoutFailure = (
  error: Error
): IPureAction<ActionTypes.LOGOUT, Error> => ({
  type: ActionTypes.LOGOUT,
  payload: error,
});

// Get all
export const getUsers = async () => {
  store.dispatch(getUsersRequest());

  try {
    const response = await userService.getAll();
    return store.dispatch(getUsersSuccess(response));
  } catch (error) {
    return store.dispatch(getUsersFailure(error));
  }
};

export const getUsersRequest = (): IPureAction<
  ActionTypes.GET_USERS_REQUEST,
  any
> => ({
  type: ActionTypes.GET_USERS_REQUEST,
  payload: undefined,
});

export const getUsersSuccess = (
  data: IUser
): IPureAction<ActionTypes.GET_USERS_SUCCESS, IUser> => ({
  type: ActionTypes.GET_USERS_SUCCESS,
  payload: data,
});

export const getUsersFailure = (
  error: Error
): IPureAction<ActionTypes.GET_USERS_FAILURE, Error> => ({
  type: ActionTypes.GET_USERS_FAILURE,
  payload: error,
});

// Edit item
export const editUser = async (user: IUser) => {
  store.dispatch(editUserRequest(true));

  try {
    const response = await userService.update(user);
    return store.dispatch(editUserSuccess(user));
  } catch (error) {
    return store.dispatch(editUserFailure(error));
  }
};

export const editUserRequest = (
  editing: boolean
): IPureAction<ActionTypes.EDIT_USER_REQUEST, boolean> => ({
  type: ActionTypes.EDIT_USER_REQUEST,
  payload: editing,
});

export const editUserSuccess = (
  user: IUser
): IPureAction<ActionTypes.EDIT_USER_SUCCESS, IUser> => ({
  type: ActionTypes.EDIT_USER_SUCCESS,
  payload: user,
});

export const editUserFailure = (
  error: Error
): IPureAction<ActionTypes.EDIT_USER_FAILURE, Error> => ({
  type: ActionTypes.EDIT_USER_FAILURE,
  payload: error,
});

// Remove item
export const deleteUser = async (id: number) => {
  store.dispatch(deleteUserRequest(id));

  try {
    userService.delete(id);
    return store.dispatch(deleteUserSuccess(id));
  } catch (error) {
    return store.dispatch(deleteUserFailure(error));
  }
};

export const deleteUserRequest = (
  id: number
): IPureAction<ActionTypes.REMOVE_USER_REQUEST, number> => ({
  type: ActionTypes.REMOVE_USER_REQUEST,
  payload: id,
});

export const deleteUserSuccess = (
  id: number
): IPureAction<ActionTypes.REMOVE_USER_SUCCESS, number> => ({
  type: ActionTypes.REMOVE_USER_SUCCESS,
  payload: id,
});

export const deleteUserFailure = (
  error: Error
): IPureAction<ActionTypes.REMOVE_USER_FAILURE, Error> => ({
  type: ActionTypes.REMOVE_USER_FAILURE,
  payload: error,
});
