import React, { useState, FormEvent, useEffect } from "react";
import { RouteComponentProps } from "react-router-dom";

import { getUsers, register, editUser, deleteUser } from "store/users/actions";
import { useTypedSelector } from "store/_reducers";
import { Roles, IUser, IUserState } from "data/user";
import Input from "./UI/Input";
import Header from "components/UI/Header";

type Props = RouteComponentProps<void, {}, IUserState>;

export const Admin: React.FC<Props> = () => {
  const users = useTypedSelector((state) => state.userReducer.users);
  const [edit, setEdit] = useState(false);
  const initialState: IUser = {
    id: 0,
    name: "",
    email: "",
    password: "",
    job: "Front-end developer",
    location: "Leeuwarden",
    role: "Admin",
  };

  const [inputFields, setInputFields] = useState(initialState);
  const [submitted, setSubmitted] = useState(false);

  // Get users for the first time
  useEffect(() => {
    getUsers();
  }, []);

  useEffect(() => {
    if (!edit) {
      setInputFields(initialState);
    }
  }, [edit]);

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    setSubmitted(true);
    if (inputFields && !edit) {
      register(inputFields);
      setInputFields(initialState);
    } else {
      editUser(inputFields);
    }
  };

  const handleEditUser = (user: IUser) => {
    setEdit(true);
    setInputFields(user);
  };

  const handleRemoveUser = (id: number) => {
    deleteUser(id);
  };

  return (
    <section className="admin-page">
      <form name="form" className="register-form" onSubmit={handleSubmit}>
        <h2>New user</h2>
        <div
          className={
            "form-group" + (submitted && !inputFields.name ? " has-error" : "")
          }
        >
          <label htmlFor="name">Name</label>
          <Input
            id="name"
            name="name"
            className="form-control"
            value={inputFields.name}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, name: target.value })
            }
          />
          {submitted && !inputFields.name && (
            <div className="help-block">Name is required</div>
          )}
        </div>
        <div
          className={
            "form-group" + (submitted && !inputFields.email ? " has-error" : "")
          }
        >
          <label htmlFor="email">Email</label>
          <Input
            id="email"
            name="email"
            className="form-control"
            value={inputFields.email}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, email: target.value })
            }
          />
          {submitted && !inputFields.email && (
            <div className="help-block">Email is required</div>
          )}
        </div>
        <div
          className={
            "form-group" +
            (submitted && !inputFields.password ? " has-error" : "")
          }
        >
          <label htmlFor="password">Password</label>
          <Input
            type="password"
            id="password"
            name="password"
            className="form-control"
            value={inputFields.password}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, password: target.value })
            }
          />
          {submitted && !inputFields.password && (
            <div className="help-block">Password is required</div>
          )}
        </div>
        <div>
          <label htmlFor="role">Role</label>
          <select
            id="role"
            value={inputFields.role}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, role: target.value as Roles })
            }
          >
            <option value="User">User</option>
            <option value="Admin">Admin</option>
          </select>
        </div>
        <div className="form-group">
          <button className="btn btn-primary">
            {edit ? "Edit" : "Add new"}{" "}
            {inputFields.role == "User" ? "User" : "Admin"}
          </button>
          {edit && <button onClick={() => setEdit(false)}>Cancel</button>}
        </div>
      </form>
      <h3>All registered users:</h3>
      {users && (
        <ul>
          {users.map((user: IUser) => (
            <li key={user.id}>
              <a onClick={() => handleEditUser(user)}>
                {user.name + " " + " | role: " + user.role}
              </a>
              <span>
                {" "}
                - <a onClick={() => handleRemoveUser(user.id)}>Delete</a>
              </span>
            </li>
          ))}
        </ul>
      )}
    </section>
  );
};

export default Admin;
