import React, { useState } from "react";
import { headerIcons } from "data/featherIcons";

interface IHeaderData {
  title: string;
  search: string;
  filters: string[]
}

interface IProps {
  headerData: IHeaderData;
  searchTerm: string;
  handleFieldChange: any;
  setNewMode: any;
  count?: number;
}

export const Header: React.FunctionComponent<IProps> = (props) => {
  const {
    headerData,
    searchTerm,
    handleFieldChange,
    setNewMode,
    count,
  } = props;
  const [searchIcon, plusIcon] = headerIcons;
  const [selectedFilter, setSelectedFilter] = useState(0);

  const setFilter = (index: number) => {
    setSelectedFilter(index);
  };

  return (
    <div className="header">
      <div className="header-state">
        <div className="header-state-title">{headerData.title}</div>
        <div className="header-state-count">
          <i>{count}</i>
        </div>
        <div className="header-state-new">
          <a onClick={() => setNewMode(true)}>{plusIcon}</a>
        </div>
      </div>
      <div className="header-search">
        <input
          type="text"
          placeholder={"Search for " + headerData.search}
          id="searchTerm"
          value={searchTerm}
          onChange={handleFieldChange}
        />
      </div>
      <div className="header-filter">
        {headerData.filters.map((item, index) => (
          <div
            key={index}
            className={selectedFilter === index ? "item selected" : "item"}
            onClick={() => setFilter(index)}
          >
            {item}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Header;
