import React, { useState } from "react";
import { IActivity, IActivityState } from "data/activity";
import { listIcons } from "data/featherIcons";

interface IProps {
  collapseState: any;
  toggleCollapse: any;
}

export const Collapse: React.FunctionComponent<IProps> = (props) => {
  const { collapseState, toggleCollapse } = props;
  // const [status, setStatus] = useState(false)
  const [arrowLeftIcon, arrowRightIcon] = listIcons;

  return (
    <div
      className="collapsible"
      onClick={toggleCollapse}
    >
      {collapseState ? arrowRightIcon : arrowLeftIcon}
    </div>
  );
};

export default Collapse;
