import React, { useState, FormEvent, useEffect } from "react";
import { RouteComponentProps } from "react-router-dom";

import { getUsers, register, editUser, deleteUser } from "store/users/actions";
import { useTypedSelector } from "store/_reducers";
import { IUser, IUserState } from "data/user";
import { useCollapse } from "hooks/useCollapse"
import Header from "components/UI/Header";
import List from "components/UI/List";
import Collapse from "components/UI/Collapse";
import SideView from "components/Users/SideView";
import NewSideView from "components/Activities/NewSideView";

import { useFormFields } from "hooks/useFormFields";
import { usersHeaderData } from "data/filters";

type Props = RouteComponentProps<void, {}, IUserState>;

export const Admin: React.FC<Props> = () => {
  const users = useTypedSelector((state) => state.userReducer.users);
  const [selected, setSelected] = useState<IUser>(users[0]);
  const [searchResults, setSearchResults] = useState(users);

  const [newMode, setNewMode] = useState(false);
  const [collapseState, toggleCollapse] = useCollapse();
  const [deleting, setDeleting] = useState(false);

  // Kan beter in de header
  const [fields, handleFieldChange] = useFormFields({
    searchTerm: "",
  });

  // Find object in array by value
  useEffect(() => {
    const results = users.filter((obj: any) => {
      return Object.keys(obj).some((key) => {
        if (obj[key] !== null) {
          const tempKey = obj[key].toString().toLowerCase();
          const tempSearch = fields.searchTerm.toLowerCase();
          return tempKey.includes(tempSearch);
        }
      });
    });
    setSearchResults(results);
  }, [fields.searchTerm]);

  // Update searchResults if activities has been updated, couse we are mapping the searchResults
  useEffect(() => {
    setSearchResults(users);
    // if (deleting) {
    setSelected(users[0]);
    setDeleting(false);
    // }
  }, [users]);

  return (
    <section className={collapseState ? "container collapsed" : "container"}>
      {/* LEFT */}
      <div className="content-left">
        <Header
          headerData={usersHeaderData}
          searchTerm={fields.searchTerm}
          handleFieldChange={handleFieldChange}
          setNewMode={setNewMode}
          count={users.length}
        />
        <List
          listItems={searchResults}
          searchTerm={fields.searchTerm}
          setSearchResults={setSearchResults}
          selected={selected}
          setSelected={setSelected}
          setNewMode={setNewMode}
        />
      </div>
      <div className="content-collapse">
        <Collapse
          collapseState={collapseState}
          toggleCollapse={toggleCollapse}
        />
      </div>

      {/* RIGHT */}
      <div className="content-right">
        {
          <SideView
            selected={selected}
            setSelected={setSelected}
            setDeleting={setDeleting}
          />
        }
      </div>
    </section>
  );
};

export default Admin;
